                this.backItem = false
              }
            } else {
              Toast('')
            }
          }
        }
      } 
    },
    stopFlagFn (item, index) {
      let that = this
      this.currentIndex = index
      this.stopFlag = false
      if (this.moveObj.y > 0 && this.stopFlag) {
        this.getStyle(item)
      }
      if (!this.backItem) {
        Toast('')
      } else {
        this.fullImgUrl = this.coppyArray(this.copyFuImg)
        setTimeout(() => {
          let newItem = {x: '1.25', y: 1, w: '5', h: '8.5', src: item.src}
          if (item.type == 'positive') {
            newItem.src = require('../../../static/images/talisman/guide/ffffffff.png')
          } else {
            newItem.src = require('../../../static/images/talisman/guide/fff.png')
          }
        this.currentImg = newItem
        },100)
        setTimeout(() => {
          this.larger = true
        }, 500)
        setTimeout(()=>{
          this.checkBegflag = true
          this.checkedBeg = this.randomImg[Math.floor(Math.random()*this.randomImg.length)]
        }, 1500)
      }
    },
    returnHome () {
      // 
      this.smallTreeFlag = 2
      this.toogeleFlag = false
      setTimeout(() => {
        this.dispearFlag = true
         this.show = false
      }, 500)
    },
    makeFU () {
      this.$router.push({path: "/makeFU"})
    },
    /**
     * [rotateImg ]
     * @author lilymaruko
     * @return   {[type]}                 [description]
     */
    rotateImg () {
      this.clickBegflag = true
    },
    returnTree (index) {
      this.larger = false
      this.checkBegflag = false
      if (index) {
        this.$store.dispatch('setText', '')
        this.$store.dispatch('setImgIndex', 0)
      }
      console.log(this.fullImgUrl, 427)
    },
    shareImgTip () {
      var _this = this;
      setTimeout(()=>{
        var shareContent = $("#picture2")[0]; //  (dom  
        var width = shareContent.offsetWidth; // (dom 
        var height = shareContent.offsetHeight; // (dom 
        var offsetTop = shareContent.offsetTop; //
        var canvas = document.createElement('canvas'); //canvas 
        var context = canvas.getContext('2d');
        var scaleBy = this.getPixelRatio(context); // ()
        canvas.width = width * scaleBy; // dom 
        canvas.height = (height + offsetTop) * scaleBy; // 
        context.scale(scaleBy, scaleBy);

        var opts = {
            allowTaint: true, //
            tainttest: true, //
            scale: scaleBy, // scale 
            canvas: canvas, // canvas
            logging: true, //false
            width: width, //dom 
            height: height //dom 
        }
        html2canvas(shareContent, opts).then(function(canvas) {
            // var body = document.getElementsByTagName("body");
            // body[0].appendChild(canvas);
            // var img = Canvas2Image.convertToImage(canvas, canvas.width, canvas.height);
            // var base64 = canvas.toDataURL();
            _this.setPicUrl(canvas);
            _this.picId = _this.picurl.toDataURL()
            // console.log(base64);
            // sessionStorage.setItem("picurl", base64);
            // console.log('', img);
            // mianPic.appendChild(img);
            // $("#testSrc").append(img);
            _this.loadingFlag = true;
        });
      }, 1000);
    },
    getPixelRatio: function(context) {
      var backingStore = context.backingStorePixelRatio ||
          context.webkitBackingStorePixelRatio ||
          context.mozBackingStorePixelRatio ||
          context.msBackingStorePixelRatio ||
          context.oBackingStorePixelRatio ||
          context.backingStorePixelRatio || 1;
      return (window.devicePixelRatio || 1) / backingStore;
    }
  },
  mounted () {
    this.copyFuImg = this.coppyArray(this.fullImgUrl)
    if (this.$store.state.imgText) {
      if (this.$store.state.imgText.length > 5) {
        this.eightflag = true
      }
      this.show = true
      this.dispearFlag = false
      this.larger = true
      this.checkBegflag = false
      this.toogeleFlag = true
      this.smallTreeFlag = 1
      this.checkBegflag = true
      this.checkedBeg = this.templateUrl[this.$store.state.imgIndex]
    }
    let today = new Date();
    this.dateObj = calendar.solar2lunar(today.getFullYear(), today.getMonth() + 1, today.getDate())
    // ios
    let width = window.screen.width
    let height = window.screen.height
    // ios
    if (device.isAndroidDevice()) {
      this.deviceFlag = 'android'
      if (width <= 360 && height <= 640) {
        this.phoneType="s"
      }
      if (width >= 360 && height >= 750) {
        this.phoneType = "x"
      }
    }
    if (device.isiOSDevice()) {
      this.deviceFlag = 'ios'
      if (width == 414 && height == 736) {
        this.phoneType = "s"
      }
      if (width <= 375 && height <= 667) {
        this.phoneType = "s"
      }
      if (width == 375 && height >= 800) {
        this.phoneType = "x"
      }
    }
    const that = this
    window.onresize = () => {
      return (() => {
        window.fullHeight = document.documentElement.clientHeight
        that.fullHeight = window.fullHeight
      })()
    }
  },
  computed: {
    ...mapGetters([
      'picurl'
    ])
  }
}
</script>
